* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    justify-content: center;
  }
  
  html {
    background-color: rgb(7, 0, 46);
  }
  
  .header {
    background-color: rgb(4, 13, 111);
    padding: 50px;
    text-align: center;
  }

  .header h1 {
    font-size: larger;
    color: white;
    font-weight: bold;
  }
  
  .header input{
    padding: 10px;
    margin: 20px;
    border-radius: 50px;
    border: 0;
    font-size: large;
  }
  
  .header button {
    padding: 15px;
    margin-left: 10px;
    border-radius: 100px;
    border: none;
    background-color: rgb(146, 2, 2);
    color: white;
    font-weight: bold;
  }
  
  .header button:hover {
    background-color: rgb(248, 248, 0);
    color: rgb(19, 2, 93);
  }
  
  .list-items {
    text-align: center;
  }
  
  .list-items button{
    color: yellow;
    padding: 10px;
    margin-left: 20px;
    background-color: rgb(39, 53, 99);
    border: 0;
    border-radius: 50%;
  }
  
  .list-items ul{
    padding: 10px;
    background-color: rgb(81, 1, 1);
  }
  
  .list-items ul li{
    padding: 10px;
    font-size: larger;
    border-bottom: 2px solid black;
    color: white;
  }

  